@function set($color) {
  $return: 0deg, 0%, 0%;
  @debug "color: #{$color}";

  $hue: hue($color);
  $saturation: saturation($color);
  $lightness: lightness($color);
  $alpha: alpha($color);

  @if ($alpha == 1) {
    $alpha: "";
  } @else {
    $alpha: unquote(", " + $alpha); //256 -- max 16x value (FF)
  }
  $return: $hue, $saturation, $lightness $alpha;

  @return $return;
}

@function use($color, $opacity: false) {
  $result: Hsla((var($color)));

  @if ($opacity) {
    $result: Hsla(var($color), $opacity);
  }

  @return $result;
}

@function spacing($value) {
  @return $spacing * $value;
}
